.form__input {
  display: block;
  margin: 0;

  min-height: 27px;
  width: 100%;

  background: none;
  box-shadow: none;
  border: none;

  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
}

.form__input:focus {
  outline: 4px groove #4a84eb80;
  border-radius: 2px;
}
